<template>
  <div style="width: 100%; height: 80vh">
    <div
      style="
        width: 98%;
        height: 10%;
        display: flex;
        align-items: center;
        font-size: 2.5vh;
      "
    >
      <div style="margin-left: 3%">
        <el-select
          v-model="value"
          placeholder="请选择"
          style="
            position: relative;
            left: -38%;
            border: 1px solid rgb(233, 233, 233);
            width: 50%;
          "
        >
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </div>
      <div style="margin-right: 3%">
        当前超级企业为{{ state }}
        <button
          @click="getState"
          style="
            width: 12vh;
            height: 6vh;
            border: none;
            background-color: rgb(255, 255, 255);
          "
        >
          <u style="color: rgb(42, 171, 255)">停用超级企业</u>
        </button>
      </div>
    </div>
    <div
      style="
        width: 98%;
        height: 30%;
        border: 1px solid rgb(228, 228, 228);
        display: flex;
      "
      v-if="data"
    >
      <div class="left_div">
        <p>签单</p>
        <p>企业类型</p>
      </div>
      <div class="left_div">
        <p>{{ data.maxnum }}</p>
        <p>最大坐席数</p>
      </div>
      <div class="left_div">
        <p>{{ data.maxconcurrency }}</p>
        <p>最大登录并发总数</p>
      </div>
      <div class="left_div">
        <p>停用</p>
        <p>API功能</p>
      </div>
      <div class="left_div">
        <p>启用</p>
        <p>录音</p>
      </div>
    </div>
    <div v-else></div>
    <div style="width: 98%; height: 60%; margin-top: 1%">
      <el-tabs v-model="activeTab">
        <el-tab-pane label="用户管理" name="first">
          <div
            style="
              width: 100%;
              height: 94%;
              display: flex;
              border: 1px solid rgb(228, 228, 228);
            "
            v-if="data"
          >
            <div class="bottom_div">
              <div class="bottom_div_div">
                <div class="div_show_left">超级企业名称</div>
                <div class="div_show">{{ data.ename }}</div>
              </div>
              <div class="bottom_div_div">
                <div class="div_show_left">企业域名</div>
                <div class="div_show">{{ data.ename }}</div>
              </div>
              <div class="bottom_div_div">
                <div class="div_show_left">超级管理员账号</div>
                <div class="div_show">
                  {{ data.adminname }}
                </div>
              </div>
            </div>
            <div class="bottom_div">
              <div class="bottom_div_div">
                <div class="div_show_left">超级企业ID</div>
                <div class="div_show">{{ data.num }}</div>
              </div>
              <div class="bottom_div_div">
                <div class="div_show_left">最大坐席数</div>
                <div class="div_show">{{ data.maxnum }}</div>
              </div>
              <div class="bottom_div_div">
                <div class="div_show_left">最大登录并发总数</div>
                <div class="div_show">{{ data.maxconcurrency }}</div>
              </div>
            </div>
          </div>
          <div v-else></div>
        </el-tab-pane>
        <el-tab-pane label="配置管理" name="second">配置管理</el-tab-pane>
        <el-tab-pane label="角色管理" name="third">角色管理</el-tab-pane>
        <el-tab-pane label="定时任务补偿" name="fourth"
          >定时任务补偿</el-tab-pane
        >
      </el-tabs>
    </div>
  </div>
</template>

<style scoped>
.left_div > p:first-child {
  font-size: 30px;
}

p {
  white-space: nowrap; /* 不换行 */
  overflow: hidden; /* 隐藏溢出部分 */
  text-overflow: ellipsis; /* 显示省略号 */
  width: 120px; /* 设置固定宽度 */
}

.div_show {
  width: 40%;
  text-align: left;
  margin-left: 10%;
}

.div_show_left {
  width: 50%;
  height: 6vh;
  text-align: right;
}

.left_div {
  width: 19%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.bottom_div {
  width: 50%;
  height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: center;
}

.bottom_div_div {
  width: 100%;
  display: flex;
  margin-top: 6vh;
}
</style>

<script>
import { getSuper, getState } from "../../api/login_http";
import { mapState } from "vuex"; // 导入 mapState 函数

export default {
  data() {
    return {
      data: null,
      options: [
        {
          value: "选项1",
          label: "客户端",
        },
        {
          value: "选项2",
          label: "cr-02",
        },
      ],
      value: "",
      getid: { id: localStorage.getItem("id") },
      activeTab: "first",
      state: "启用状态",
    };
  },
  computed: {
    ...mapState({
      params: (state) => state.params, // 使用 mapState 获取 Vuex 中的参数
    }),
  },
  created() {
    if (this.getid != "") {
      this.getSuper(this.getid);
    } else {
      this.getSuper(); // 调用获取业务数据的方法
      const id = localStorage.getItem("id");
      console.log(id);
    }
  },
  mounted() {
    this.value = this.options[0].value;
  },
  watch: {
    params: {
      handler(newParams) {
        // 监听参数变化
        const id = { id: newParams };
        this.getSuper(id); // 根据新参数获取数据
      },
      deep: true, // 深度监听
    },
  },
  methods: {
    getSuper(id) {
      if (this.state == "启用状态") {
        this.state = "停用状态";
      } else {
        this.state = "启用状态";
      }
      getSuper(id).then((res) => {
        this.data = res.data;
        if (this.data.status == 1) {
          this.state = "启用状态";
        } else {
          this.state = "停用状态";
        }
      });
    },
    getState() {
      const obj = { id: this.data.id, status: !this.data.status };
      getState(obj).then(() => {
        if (this.state == "启用状态") {
          this.state = "停用状态";
        } else {
          this.state = "启用状态";
        }
      });
    },
    formatDate(timestamp) {
      // 如果时间戳是以秒为单位的，将其乘以 1000 转换为毫秒
      const milliseconds = timestamp * 1000;
      const date = new Date(milliseconds);
      const year = date.getFullYear();
      const month = String(date.getMonth() + 1).padStart(2, "0"); // Month starts from 0
      const day = String(date.getDate()).padStart(2, "0");
      return `${year}-${month}-${day}`;
    },
  },
};
</script>

